import React, { Component } from 'react';
import './App.css';
import Navigation from "./js/navigation";

var sectionStyle = {
    width: "100%",
    height: "100%",
    minHeight: "1000px",
    marginTop: "64px",
    backgroundColor: "#FFFFF0",
    backgroundSize: "100% 100%",
};
var otherSectionStyle = {
    width: "100%",
    height: "100%",
    minHeight: "1000px",
    marginTop: "64px",
    backgroundColor: "#B0B0B0",
    backgroundSize: "100% 100%",
};

var hide = {
    display: "none",
}

var show = {
    display: "inline"
}

let tmp = 0;

class App extends Component {

    state = {
        introductionStyle: show,
        booksStyle: hide,
        backStyle: sectionStyle,
    };

    showIntroduction(){
        this.setState({
            introductionStyle: show,
            booksStyle: hide,
        })
    };

    showBooks(){
        this.setState({
            introductionStyle: hide,
            booksStyle: show,
        })
    };

    changeStyle(){
        tmp++;
        this.setState({
            backStyle: (tmp % 2) === 1 ? otherSectionStyle : sectionStyle,
        })
    };

    render() {
        return (
            <div className="App" style={this.state.backStyle}>
                <div id="navigation">
                    <Navigation
                        showIntro={() => this.showIntroduction()}
                        showBooks={() => this.showBooks()}
                        changeStyle={() => this.changeStyle()}
                    />
                </div>
                <div>
                    <div id="introduction" style={this.state.introductionStyle}/>
                    <div id="books" style={this.state.booksStyle}/>
                </div>
            </div>
        );
    }
}

export default App;
